import React, { PropTypes } from 'react';
import { Title4Top, Total, Highlight } from '../styledComponents';

const OrderSummary = ({ rdv, promoCode, deliveryFee, total, totalPaid }) => {
  const rdvDate = rdv.format('D MMMM');
  const rdvBegin = rdv.format('H[h]mm');
  const rdvEnd = rdv.clone().add(30, 'minute').format('H[h]mm');
  return (
    <div>
      <Title4Top>Récapitulatif</Title4Top>
      <Total>
        Rdv le <Highlight>{rdvDate}</Highlight> entre <Highlight>{rdvBegin}</Highlight>
        {' et '}<Highlight>{rdvEnd}</Highlight>
        {promoCode && promoCode.value ?
          <div>
            <p>Total des retouches : {total}€</p>
            <p>Code promo : -{promoCode.value}{promoCode.unit === 'EUROS' ? '€' : '%'}</p>
            <p>Frais de livraison : {deliveryFee}€ (*)</p>
            <p>Montant total : {totalPaid}€</p>
          </div> :
          <div>
            <p>Total des retouches : {total}€</p>
            <p>Frais de livraison : {deliveryFee}€ (*)</p>
            <p>Montant total : {totalPaid}€</p>
          </div>
        }
      </Total>
    </div>
  );
};

OrderSummary.propTypes = {
  rdv: PropTypes.shape({}).isRequired,
  promoCode: PropTypes.shape({}),
  deliveryFee: PropTypes.number.isRequired,
  total: PropTypes.number.isRequired,
  totalPaid: PropTypes.number.isRequired,
};

OrderSummary.defaultProps = {
  promoCode: null,
};

export default OrderSummary;



